$primary: #AAC8A7;
$secondary: #C9DBB2;
$tertiary: #E3F2C1;
$quaternary: #F6FFDE;
$fontcolor: rgb(50, 50, 50);

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    background-color: $primary;
}

.App {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}

.Title {
    padding: 30px 0px 20px 0px;

    h1 {
        color: $fontcolor;
        font-size: 30px;
    }
}

.Settings {
    padding: 20px;
    background-color: $secondary;
    box-shadow: 2px 2px 30px $tertiary;
    border-radius: 30px;
    margin-bottom: 20px;
    display: flex;
    width: 700px;
    height: 80px;
    align-items: center;
    justify-content: space-evenly;

    input {
        width: 70px;
        margin: 5px;
        text-align: center;
        font-size: 15px;
        padding: 5px;
        color: $fontcolor;
        background-color: $quaternary;
        border: none;
        border-radius: 10px;
        outline: none;
    }

    .error {
        background-color: rgb(248, 158, 158);
    }

    .numarea {
        div {
            letter-spacing: 3px;
            color: $fontcolor;
        }

        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .quesnum {
        div {
            letter-spacing: 3px;
            color: $fontcolor;
        }

        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .flashtime {
        div {
            letter-spacing: 3px;
            color: $fontcolor;
        }

        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .start {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        button {
            letter-spacing: 3px;
            padding: 10px;
            font-size: 16px;
            color: $fontcolor;
            background-color: $quaternary;
            border: none;
            border-radius: 10px;
            outline: none;
            cursor: pointer;

            &:hover {
                background-color: $tertiary;
                box-shadow: 1px 1px 10px gray;
            }

            &:active {
                background-color: $primary;
                box-shadow: 1px 1px 10px gray;
            }

            &:disabled {
                background-color: gray;
                cursor: auto;
            }
        }
    }
}

.FlashCalc {
    background-color: rgb(237, 233, 233);
    border-radius: 20px;
    padding: 5px 20px;
    height: 380px;
    width: 800px;
    position: relative;

    .error {
        padding: 10px;
        width: 100%;
        position: absolute;
        top: 0%;
        left: 50%;
        transform: translate(-50%, 0%);
        -webkit-transform: translate(-50%, 0%);
        -ms-transform: translate(-50%, 0%);

        div {
            margin: 5px 0px;

            p {
                letter-spacing: 5px;
                font-size: 20px;

                span {
                    color: rgb(249, 114, 114);
                    font-weight: bold;
                }
            }
        }
    }

    .game {
        height: 100%;
        position: relative;

        .num {
            color: $fontcolor;
            font-size: 160px;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            -webkit-transform: translate(-50%, -50%);
            -ms-transform: translate(-50%, -50%);
        }


        .input {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            -webkit-transform: translate(-50%, -50%);
            -ms-transform: translate(-50%, -50%);



            form {
                height: 400px;
                width: 200px;

                input {
                    position: absolute;
                    top: 20%;
                    left: 50%;
                    transform: translate(-50%, -20%);
                    -webkit-transform: translate(-50%, -20%);
                    -ms-transform: translate(-50%, -20%);
                    height: 60px;
                    width: 180px;
                    text-align: center;
                    font-size: 30px;
                    color: $fontcolor;
                    background-color: $secondary;
                    border: none;
                    border-radius: 10px;
                    outline: none;
                }

                button {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    -webkit-transform: translate(-50%, -50%);
                    -ms-transform: translate(-50%, -50%);
                    height: 60px;
                    width: 180px;
                    letter-spacing: 3px;
                    color: $fontcolor;
                    background-color: $quaternary;
                    border: none;
                    border-radius: 10px;
                    outline: none;
                    cursor: pointer;
                    font-size: 24px;

                    &:hover {
                        background-color: $secondary;
                    }

                    &:disabled {
                        background-color: gray;
                        cursor: auto;
                    }
                }
            }
        }

    }

    .hidden {
        visibility: hidden;
        height: 0;
    }


}